.form-page__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: $black;
  margin: 0;
  padding-bottom: 37px;
  @media (min-width: $tablet-width) {
    padding-bottom: 56px;
}
}
.form-page__intro {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  margin: 0;
  padding-bottom: 24px;

  @media (min-width: $tablet-width) {
    padding-bottom: 66px;
    font-size: 20px;
    line-height: 30px;
  }
}

